import React, { Component } from 'react'
import './Card.css'

class Card extends Component {
  isSelected () {
    return (this.props.card.selected) ? 'card selected' : 'card'
  }
  render () {
    const cost = this.props.card.cost.map((item,k) => {
      let pre = (typeof item === 'number')?'$':''
      return (<div className={'icon _'+item} key={this.props.card.key+'c'+k}>{pre+item}</div>)
    })
    const provides = this.props.card.provides.map((item,k) => {
      let pre = (typeof item === 'number')?'$':''
      return (<div className={'icon _'+item} key={this.props.card.key+'p'+k} title={pre+item}>{pre+item}</div>)
    })

    return (
      <div className={this.isSelected()} onClick={() => this.props.onClick()}>
        <div className='name'>{this.props.card.name}</div>
        <div className='cost'>{cost}</div>
        <div className='art'></div>
        <div className='provides'>{provides}</div>
      </div>
    )
  }
}

export default Card



