import React, { Component } from 'react'
import logo from './logo.svg'
import './App.css'
import GameArea from './GameArea'
import {DB_CONFIG} from './config/config'
import firebase from 'firebase/app'
import 'firebase/database'

const ST_BUYING_CARDS = 1
const ST_SELECTING_CARDS = 2
const ST_FINISH_SELECTING_CARDS = 3

class App extends Component {
  constructor (props) {
    super(props)
    this.app = firebase.initializeApp(DB_CONFIG)
    this.db = this.app.database().ref('/0')
    this.state = getNewGame()
  }

  componentWillMount(){
    console.log('Will mount')
    const oldState = this.state
    this.db.on('value', snap => {
      console.log(snap.val())
      this.setState(snap.val())
    })
  }

  saveState(newState){
    this.setState(newState)
    let statePer = JSON.parse(JSON.stringify(newState))
    this.db.set(statePer)
  }


  render () {
    return (
      <div className='App'>
        <GameArea
          game={this.state}
          onClick={() => this.handleClickArea(1)}
          cardClick={(card) => this.handleClickCard(card)}
          loanClick={() => this.handleClickLoan()}
          confirmSelectionClick={()=>this.handleConfirmSelectionClick()}
          resetGameClick={()=>this.resetGameClick()}
        />
        <div className='app-controls'>
          <button onClick={()=>this.resetGameClick()}>Reset</button>
          <button onClick={()=>this.addPlayerClick()}>Add Player</button>
        </div>
      </div>
    )
  }

  addPlayerClick(){
    try{
      var newState = JSON.parse(JSON.stringify(this.state))
      let name = window.prompt('Type the player name','Your name here')
      let np = getNewPlayer(name.length>0?name:'Meat Meeple');
      newState.players.push(np)
      this.saveState(newState)
      console.log(np.key, " added to the game")
    } catch(e) {
      throw new Exception(e);
    }
  }

  resetGameClick(){
    const newState = getNewGame()
    this.saveState(newState)
  }
  handleConfirmSelectionClick(){
    try{
      var newState = JSON.parse(JSON.stringify(this.state))
      console.log(newState.players[newState.currentPlayer].name, " clicked Confirm Selection")
      if (newState.status === ST_SELECTING_CARDS){
        checkCardCostRequiriments(newState)
        payForTheCard(newState)
        drawCardFromDeckToOffer(newState)
        advanceToNextPlayer(newState)
        startTurn(newState)
        gainFromIncome(newState)
        resetLoans(newState)
        setStatus(newState, ST_BUYING_CARDS)
      } else {
        return
      }
    } catch (e) {
      console.log(e)
      cancelCardConstruction(newState)
      setStatus(newState, ST_BUYING_CARDS)
    } finally{
      clearSelectedCards(newState)
      this.saveState (newState)
    }

  }
  handleClickArea (i) {
    console.log()
  }

  handleClickCard (card) {
    if(this.state.status === ST_BUYING_CARDS){
        this.handleStartBuyCard(card)
    } else if(this.state.status === ST_SELECTING_CARDS){
        this.handleSelectCard(card)
    } else if(this.state.status === ST_FINISH_SELECTING_CARDS){
        this.handleBuyCard(card)
    }
  }

  handleStartBuyCard(card){
    const newState = JSON.parse(JSON.stringify(this.state))
    console.log(newState.players[newState.currentPlayer].name, " clicked ", card)
    try{
        startBuyCard(newState, card)
    } catch (e) {
      console.log(e)
    } finally{

      this.saveState(newState)
    }
  }
  handleSelectCard(card){
    const newState = JSON.parse(JSON.stringify(this.state))
    console.log(newState.players[newState.currentPlayer].name, " clicked ", card)
    try {
      selectCard(newState, card)
    } catch (e) {
      console.log(e)
    } finally{
      this.saveState(newState)
    }
  }
  handleBuyCard(card){
    const newState = JSON.parse(JSON.stringify(this.state))
    console.log(newState.players[newState.currentPlayer].name, " clicked ", card)
    try{
      if(checkCardCostRequiriments(newState, card)){
        payForTheCard(newState, card)
        drawCardFromDeckToOffer(newState)
        advanceToNextPlayer(newState)
        startTurn(newState)
        gainFromIncome(newState)
        resetLoans(newState)
      } else {
        newState.status = ST_SELECTING_CARDS
      }
    } catch (e) {
      console.log(e)
    } finally{

      this.saveState(newState)
    }
  }

  handleClickLoan(){
    const newState = JSON.parse(JSON.stringify(this.state))
    console.log(newState.players[newState.currentPlayer].name, " clicked loan.")
    try{
      getALoan(newState)
    }catch (e) {
      console.log(e)
    } finally{

      this.saveState(newState)
    }
  }
}

function getNewPlayer(name){
  return {key: 'player'+Math.floor(Math.random()*10000000), name:name, income:0, money: 0, cards:[], building: []}

}
function getNewGame(){
  console.log('New Game')
  let state = {
    status: ST_BUYING_CARDS,
    loans: 2,
    currentPlayer: 0,
    players: [],
    deck: createDeckCards(),
    cards: [],
    discard: [],
  }
  shuffleDeck(state)
  drawCardFromDeckToOffer(state)
  drawCardFromDeckToOffer(state)
  drawCardFromDeckToOffer(state)
  drawCardFromDeckToOffer(state)
  drawCardFromDeckToOffer(state)
  drawCardFromDeckToOffer(state)
  return state
}


function setStatus(state, newStatus){
    console.log(state.players[state.currentPlayer].name, " clicked Confirm Selection")
    state.status = newStatus
}

function getSelectedCards(state){
  var selectedCards = []
  selectedCards = selectedCards.concat(state.cards.filter(card => card.selected))
  state.players.forEach(p => {
    if(p.cards === undefined) p.cards = []
    selectedCards = selectedCards.concat(p.cards.filter(cp => cp.selected))
  })
  return selectedCards
}

function clearSelectedCards(state){
  let selectedCards = getSelectedCards(state)
  selectedCards.forEach(card => {
    card.selected = false;
  })
}
function selectCard (state, card) {
  //card.selected = !card.selected;
  const idx = state.cards.findIndex(item => item.key === card.key)
  if(idx>=0){
    state.cards[idx].selected = !state.cards[idx].selected
  }
  state.players.forEach((p)=>{
    const idx = p.cards.findIndex(item => item.key === card.key)
    if(idx>=0){
      p.cards[idx].selected = !p.cards[idx].selected
    }
  })
  console.log(
    state.players[state.currentPlayer],
    card.selected?' selected ':' deselected ',
    card)
    console.log(getSelectedCards(state))
}

function shuffleDeck(state){
  let currentIndex = state.deck.length, temporaryValue, randomIndex;
  while (0 !== currentIndex) {
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;
    temporaryValue = state.deck[currentIndex];
     state.deck[currentIndex] = state.deck[randomIndex];
     state.deck[randomIndex] = temporaryValue;
  }
}

function getALoan(state){
  if(state.loans <= 0){
    console.log(state.players[state.currentPlayer].name, ' cannot take more loans.')
    throw new Exception(state.players[state.currentPlayer].name+ ' cannot take more loans.')
  }
  state.players[state.currentPlayer].money += 3
  state.players[state.currentPlayer].income -= 1
  state.loans--
  console.log(state.players[state.currentPlayer].name, ' toke a loan.')
}

function startBuyCard(state, card) {
  const idx = state.cards.findIndex(item => item.key === card.key)
  if( idx < 0 ) {
    console.log(card, 'is not in the offer.')
    throw new Exception(card.name + 'is not in the offer.')
  }
  if(card.cost[0] > state.players[state.currentPlayer].money){
    console.log(state.players[state.currentPlayer].name, 'cannot afford ', card )
    throw new Exception (state.players[state.currentPlayer].name + ' cannot afford ' + card.name )
  }
  card = state.cards.splice(idx, 1)[0]
  state.players[state.currentPlayer].building.push(card)
  state.status = ST_SELECTING_CARDS
  console.log(state.players[state.currentPlayer].name, ' try to buy ', card )
}

function checkCardCostRequiriments(state){
  const player = state.players[state.currentPlayer]
  const selected = getSelectedCards(state)
  const card = player.building[0]
  console.log('Cost to pay:', card.cost)
  console.log('Selected cards:', selected)
  if(selected.indexOf(card)>=0){
    throw new Exception(card.name+' cannot be used to pay itself!')
  }
  selected.forEach(c => {
    if(state.cards.indexOf(c)>=0){
      throw new Exception('Cannot be use cards in offer pay!')
    }
  })
  let provided = []
  selected.forEach((c) => {
    provided = provided.concat(c.provides.filter(it => typeof it !== 'number'))
  })
  console.log('Resources selected:', provided)
  if(card.cost[0]>player.money){
    throw new Exception(player.name+' cannot afford '+card.name+'!')
  }
  let hCost = {}
  card.cost.forEach(c => {
    if(typeof c === 'number') return;
    if(hCost[c] === undefined){
      hCost[c] = 1
    } else {
      hCost[c]++
    }
  })
  let hProv = {}
  provided.forEach(c => {
    if(typeof c === 'number') return;
    if(hProv[c] === undefined){
      hProv[c] = 1
    } else {
      hProv[c]++
    }
  })
  console.log('Cost hist', hCost)
  console.log('Prov hist', hProv)
  for (var c in hCost) {
    if (hCost.hasOwnProperty(c)) {
      if(hProv[c]=== undefined || hCost[c]>hProv[c]){
        throw new Exception('Missing '+(c)+' from selected cards!' )
      }
    }
  }
}

function payForTheCard(state){
  const player = state.players[state.currentPlayer]
  const selected = getSelectedCards(state)
  const card = player.building[0]
  player.money -= card.cost[0]
  player.cards.push(player.building.pop())
  for (var pi = state.players.length-1; pi >= 0; pi--) {
    let p = state.players[pi]
    for (var i = p.cards.length-1; i >= 0; i--) {
      if(p.cards[i].selected){
        console.log('Discarded ', p.cards[i].name, 'from ', p.name)
        onDiscard(p.cards[i], p, player, state)
        state.discard.push(p.cards.splice(i,1))
      }
    }
  }
  console.log(state.players[state.currentPlayer].name, ' buyed ', card )
}

function resetLoans(state) {
  state.loans = 2
}

function startTurn(state) {
  console.log(state.players[state.currentPlayer].name, " start his turn ")
}

function gainFromIncome(state){
      console.log(state.players[state.currentPlayer].name, " gains ", state.players[state.currentPlayer].income, 'from income.')
      state.players[state.currentPlayer].money += state.players[state.currentPlayer].income
}

function advanceToNextPlayer(state){
    state.currentPlayer = ((state.currentPlayer + 1 < state.players.length)? state.currentPlayer + 1 : 0)
    console.log(state.players[state.currentPlayer].name, ' is now the active player.')
}

function drawCardFromDeckToOffer(state){
  const card = state.deck.pop()
  if(card){
    state.cards.push(card)
  }
}

function cancelCardConstruction(state){
  state.cards = state.cards.concat(state.players[state.currentPlayer].building)
  state.players[state.currentPlayer].building = []
}
function Exception(m){
  this.m = m;
}

function createCard(array, n, model){
  let name = model.name.replace(/ /g, '')
  let onDiscard = (model.onDiscard)||function(){}
  for (let i = 1; i <= n; i++) {
    let k = i
    array.push(Object.assign({key:name+k, onDiscard:onDiscard}, model))
  }
}
function onDiscard(card, owner, player, state){
  console.log(owner.name + ' discarded '+card.name+' and gets:')
  card.provides.forEach(d=>{
    if(typeof d === 'number'){
      owner.money += d
      console.log(owner.name+ ' gains $'+d)
    }
    if(d === 'income'){
      owner.income += 1
      console.log(owner.name+ ' increses his income.')
    }
  })
}
function createDeckCards(){
  let cards = []
  createCard(cards, 4, {name: 'Quarry', selected: false, cost: [2], provides:['stone']})
  createCard(cards, 4, {name: 'Gold Mine', selected: false, cost: [3], provides:['gold']})
  createCard(cards, 4, {name: 'Greengrocer', selected: false, cost: [1, 'food'], provides:['food']})
  createCard(cards, 4, {name: 'Pasture', selected: false, cost: [1, 'wood'], provides:['hay']})
  createCard(cards, 4, {name: 'Coffee', selected: false, cost: [2, 'wood'], provides:['cofee']})
  createCard(cards, 4, {name: 'Vegetables', selected: false, cost: [2, 'wood'], provides:['food']})
  createCard(cards, 4, {name: 'Contract (Food)', selected: false, cost: [2, 'food'], provides:['income']})
  createCard(cards, 4, {name: 'Contract (Coffee)', selected: false, cost: [2, 'coffee'], provides:['income','income']})
  createCard(cards, 4, {name: 'Contract (Gold)', selected: false, cost: [2, 'gold'], provides:['income','income','income']})
  createCard(cards, 4, {name: 'Inn', selected: false, cost: [1, 'wood'], provides:['income']})
  createCard(cards, 4, {name: 'Forest Area', selected: false, cost: [1], provides:[3, 'wood']})
  return cards
}
export default App



